.bg-item2{
    background-image: url(../images/main_chef_brown@2x.png);
    width: 50%;
    @media(max-width: 767px) {
        width: 100%;
        height: 460px;
        }
    @media(max-width: 320px) {
        height: 600px;
        }
}

.question{
    font-size: 20px;
    font-weight: bolder;
    margin-bottom: 30px;
    position: relative;
}
.before{
    position: absolute;
    right: 63%;
    left: 0;
    top: 50%;
    border-bottom: 1px solid #3D1101;
    
@media (max-width: 991px) {
    right: 68%;
}
@media (max-width:767px) {
    right: 74%;
}
@media (max-width:425px) {
    right: 80%;
}
}
.after{
    position: absolute;
    right: 0;
    left: 63%;
    top: 50%;
    border-bottom: 1px solid #3D1101;
    @media (max-width: 991px) {
        left: 68%;
    }
    @media (max-width:767px) {
        left: 74%;
    }
    @media (max-width:425px) {
        left: 80%;
    }
}
.card{
    width: 32%;
    margin-right: 2%;
    .card-header{
        img{
            width: 200px;
            height: 200px;
        }
    }
    .card-body{
        margin-top: 20px;
        h3{
            font-weight: 600;
            font-size: 18px;
            margin-bottom: 10px;
        }
        p{
            font-weight: 500;
        }
    }
    @media (max-width:767px) {
        width: 100%;
        margin-right: 0;
        margin-bottom: 2%;
    }
}
.chief-text{
    padding:8.48% 0 8.48% 4%;
    h2{
        font-size: 20px;
        margin-bottom: 16px;
    }
    @media(max-width: 767px) {
            padding:8.48% 0 8.48% 0%;
            width: 100%;
            color: rgba($color: white, $alpha: 1.5);
            text-align: center;  
            position: absolute;
            top: 0;
            bottom: 0;
            right: 0;
            left: 0;

        }
    @media(max-width: 320px) {
            padding: 0;
        }
}
.media{
    display: flex;
    width: 32%;
    margin-right: 2%;
    .media-header{
        img{
            width: 67px;
            height: 67px;
            margin-right: 16px;
        }
    }
    .media-body{
        h3{
            font-size: 18px;
            font-weight: bolder;
            margin-bottom: 22px;
        }
        p{
            margin-bottom: 16px;    
        }
    }
    @media(max-width: 767px) {
        width: 100%;
        margin-right: 0;
        justify-content: center;
        }
}
.map{
    width: 50%;
    margin-right: 2%;
    iframe{
        width: 100%;
        height: 100%;
    }
    @media(max-width: 767px) {
        width: 100%;
        margin-right: 0;
        iframe{
            height: 400px;
        }
        }
}
.form{
    width: 48%;
    @media(max-width: 767px) {
        width: 100%;
        }
}
.form-group{
    margin-bottom: 16px;
    label{
        display: block;
        margin-bottom: 8px;
    }
    input{
        width: 90%;
        padding: 2% ;
    }
}
h2{
    margin-bottom: 8px;
}
.input-group{
    margin-right: 3%;
}
.btn-group{
    display: flex;
    justify-content: flex-end;
}
.btn{
    color: #fff;
    padding: 8px 16px;
    margin-right: 5%;
    border-radius: 5px;
    cursor: pointer;
}
.btn-secondary{
    background-color: rgba($color: gray, $alpha: .7);
}
.btn-secondary:hover{
    background-color: rgba($color: gray, $alpha: .9);
}
.btn-warning{
    background-color: rgba($color: 	#D94600, $alpha: .8);
}
.btn-warning:hover{
    background-color: rgba($color: 	#D94600, $alpha: 1);
}
