.side-bar{
    width: 25%;
    margin-right: 2%;
    h2{
        font-weight: 500;
        font-size: 20px;    
        margin-bottom: 16px;
    }
    li{
        padding: 2%;
        border-bottom:  1px solid $primary;
        a{
            color: $primary;
            cursor: pointer;
        }
    }
    li:hover{
        background-color: rgba($color: $primary, $alpha: 1);
        a{
            color: white;
        }
    }
    @media(max-width: 767px) {
        width: 100%;
        margin-bottom: 16px;
        margin-right: 0;
        }
}
.cart-content{
    width: 73%;
    @media(max-width: 767px) {
        width: 100%;
        }
}
.search{
    margin-right: auto;
    input{
        width: 500px;
        height: 30px;
        border: 1px solid $primary;
        @media(max-width: 992px) {
            width: 380px;
            }
            @media(max-width: 767px) {
                width: 90%;
                }
    }
    span{
        background-color: $primary;
        height: 30px;
        padding-top: 4px;
    }
    @media(max-width: 767px) {
            margin-bottom: 16px;
        }
}
.cart{
    border-left: 1px solid $primary;
    padding-left: 10px;
    margin-left: 16px;
    font-size: 24px;
    margin-right:5px;
    @media(max-width: 767px) {
        margin-right: 16px;
    }
}
.cart-num{
    background-color: red;
    color: white;
    border-radius: 50% ;
    padding:0 6px;
    position: absolute;
    top: -10px;
    right: -5px;
    @media(max-width: 767px) {
        right: 4px;
    }
}

.cart-card{
    width: 31%;
    margin-right: 2%;
    box-shadow: 10px 10px 5px 0px rgba(0, 0, 0, 0.2);
    padding-bottom: 16px;
    margin-bottom: 20px;
    .card-header{
        margin-bottom: 16px;
    }
    .card-body{
        h3{
            font-size: 20px;
            font-weight: bolder;
        } 
        p{
            padding-bottom: 10px;
            border-bottom: 1px solid $primary;
            margin-bottom: 16px;
        }
        span{
            font-size: 20px;
            font-weight: bolder;
            color:$primary;
        }
    }
    .card-footer{
        margin-top: 16px;
        .btn-outline-orange{
            border: #FF5E45 2px solid;
            color:#FF5E45 ;
            width: 90%;
            display: block;
            padding: 8px 0;
            text-align: center;
        }
        .btn-outline-orange:hover{
            color: white;
            background-color:rgba($color: 	#D94600, $alpha: 1.0)
        }
    }
    @media(max-width: 992px) {
        width: 47.5%;
        margin-right:  2% ;
        }
    @media(max-width: 767px) {
        width: 100%;
        margin-right:  0 ;
        }
}