.login{
    max-width: 500px;
    text-align: center;
    margin: 0 auto;
    h2{
        font-size: 48px;
        font-weight: bolder;
    }
    p{
        font-size: 20px;
        margin-bottom: 30px;
        font-weight: bolder;
    }
    .login-card{
        padding: 8%;
        border-top:5px solid #FF5E45 ;
        box-shadow: 10px 10px 5px 0px rgba(0, 0, 0, 0.2);
        border-radius: 5px;
        margin: 0 5%;
        h3{
            font-size: 18px;
            font-weight: bolder;
        }
        label{
            margin-bottom: 4px;
            display: block;
            text-align: left;
        }
        input{
            margin-bottom: 16px;
            width: 100%;
        }
    }
    @media(max-width: 767px) {
        h2{
            font-size: 32px;
        }
        }
}