.align-items-center {
    align-items: center;
}
.bg-primary{
    background-color: $primary;
}
.bg-white{
    background-color: #fff;
}
.bg-dark{
    background-color: rgba($color: #000000, $alpha: .75);
    
}
.bg-light{  
    background-color: rgba($color: #000000, $alpha: .2);
}
.bg-cover{
    background-position: center center;
    background-size: cover;
}
.cursor-pointer{
    cursor: pointer;
}
.container {
    width: 100%;
    margin-right: auto;
    margin-left: auto;
}

@media (min-width: 576px) {
    .container {
    max-width: 540px;
    }
}

@media (min-width: 768px) {
    .container {
    max-width: 720px;
    }
}

@media (min-width: 992px) {
    .container {
    max-width: 960px;
    }
}

@media (min-width: 1200px) {
    .container {
    max-width: 1140px;
    }
}
.d-flex{
    display: flex;
}
.d-blcok{
    display: block;
}
.d-sm-block{
    @media(max-width: 767px) {
    display: block;
    }
}
.flex-column{
    flex-direction: column;
}
.flex-wrap{
    flex-wrap: wrap;
}
.h-100{
    height: 100%;
}
.h2{
    font-size: 24px;
    font-weight: bolder;
    margin-bottom: 32px;
}
.list-disc{
    list-style: disc;
}
.mb-5{
    margin-bottom: 50px;
}
.mx-5{
    @media(max-width: 767px) {
        margin-right: 5%;
        margin-left: 5%;
        }
}
.justify-content-center {
            justify-content: center;
}
.justify-content-between {
            justify-content: space-between;
}
.justify-content-around {
        justify-content: space-around;
}
.justify-content-sm-end{
    @media(max-width: 767px) {
        justify-content: flex-end;
        }
}
.position-relative {
    position: relative ;
}
.position-sm-relative{
    @media(max-width: 767px) {
        position: relative;
        }
}
.position-absolute {
    position: absolute ;
}
.pd-5{
    padding: 3% 0;
}   
.text-align-center{
    text-align: center;
}
.text-align-end{
    text-align: end;
}
.text-white{
    color: white;
}
.text-primary{
    color:$primary;
}
.vertical-align-bottom{
    vertical-align: bottom;
    
}
