//layout start
body{
    color: $primary;
    background-color: $secondary;
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
    line-height: 1.5;
}
a{
    text-decoration: none;
}
.top{
    top: 0;
    right: 0;
    left: 0;
    z-index: 1;
}

.bg-item1{
    background-image: url(../images/header_desktop@2x.png);
    height: 420px;
    max-width: 1200%    ;
}
.topmenu{
    padding: 1.4% 3.68%;
    h1{
        font-size: 24px;
        font-weight: bolder;
    }
    ul{
        width: 20%;
        @media (max-width: 991px) {
            display: none;}
    }
    li{
            margin-right: 13%;
    }

}
.ham{
    font-size: 24px;
    margin-right: 3%;
    display: none;
    @media (max-width: 991px) {
        display: block;
    }
}
.jq-list{
    left: 0;
    top: 13%;
    width: 100%;
    max-height: 0px;
    transition: all .8s;
    overflow: hidden;
    z-index: 120;
    li{
        text-align: center;
        margin-right: 0;
        border: 1px solid #000;
        background-color: #ffffff;
        padding:1%;
        a{
            color: #000;
        }

    }
    li:hover{
        background-color: red;
    }
}
.active{
    max-height: 200px;
}
.banner-text{
    padding: 2.5%;
    h2{
        font-size: 36px;
        margin-bottom: 16px;
        @media (max-width: 767px) {
            font-size: 24px;
        }
    }
}
.footer-text{
    margin-right: 21px;
    h2{
        font-weight: bolder;
        font-size: 24px;
        margin-bottom: 16px;
    }
    ul{
        margin-left: 16px;  
    }
    li{
        margin-bottom: 8px;
    }
    @media (max-width: 991px) {
        display: none;
    }
}   
.footer-logo{
    margin-left: auto;
    a{
        display: block;
        margin-bottom: 16px;
        margin-right: 4%;
        span{
            font-size: 36px;
        }
    }
    @media (max-width: 991px) {
        width: 100%;
        display:flex;
        justify-content: space-around;
        margin-left: 0;
    }
    @media (max-width: 426px) {
        a{
            img{
                width: 150px;
            }
            span{
                font-size: 24px;
            }
        }
    }
}
//layout end